
import React, { createContext, useState } from 'react'

export const estadoContext = createContext();

export const Estado = ({children}) => {

    const [imagenes, setImagenes] = useState([]);

    return (
        <estadoContext.Provider 
            value={{
                imagenes,
                setImagenes
            }}
        >
            { children }
        </estadoContext.Provider>
    )
}
