import React from 'react'

    export const Item = ( {imagen, index, itemWidth, angleUnit, coors, selected} ) => {

    if( !coors[index] ){
        return null;
    }
    
    return (
        <div 
            className={`item`} 
            style={{
                left: `${ coors[index].sx - itemWidth/2 }px`,
                top: `${ coors[index].sy - itemWidth/2 }px`,
                width:`${itemWidth}px`,
                height:`${itemWidth}px`
            }}
            >
            <img 
                src={ imagen }
                alt="imagen"
            />
        </div>
    )
}
