import React, { useContext, useEffect, useState } from 'react'
import { estadoContext } from '../context/Estado';
import { createCoors } from '../helpers/helpersRuleta';
import flecha from '../assets/flecha.png';

import { Item } from './Item';

export const Ruleta = () => {

    const { imagenes } = useContext( estadoContext );
    const [coors, setCoors] = useState([]);
    const [ girando, setGirando] = useState( 225 );

    let nImagenes = imagenes.length>0 ? imagenes.length : 1;
    const itemWidth = nImagenes>4 ? (Math.PI * 250) / nImagenes : 196;
    const angleUnit = 360/nImagenes;

    useEffect(()=>{
        createCoors(250,250,250,nImagenes, setCoors);
    },[]);

    useEffect(()=>{
        createCoors(250,250,250,nImagenes, setCoors);
    },[ imagenes ]);

    const handleGirar = () =>{
        setGirando( angleUnit * ( Math.random() * ( nImagenes - 1 ) + 1) * 10);
    }

    return (
        <div className="ruleta"
        >
            {
                imagenes.map( (imagen, index )=>( 
                    <Item 
                        imagen={imagen} 
                        index={ index }
                        key={ index }
                        coors={ coors }
                        itemWidth={itemWidth}
                        angleUnit={angleUnit}
                        />))
            }
            <button 
                className={`${ girando ? 'gradiente-calor' : 'gradiente-agua' }`}
                onClick={ ()=> handleGirar() }
                >Girar</button>
            <img src={flecha} 
                alt="flecha"
                style={{ transform: `rotate(${ girando }deg)` }} 
                className={`flecha`/*${ girando ? 'girar' : ''}`*/}
                />
        </div>
    )
}
