import React, { useContext } from 'react'
import { estadoContext } from '../context/Estado';

export const InputFile = () => {

    const { imagenes, setImagenes } = useContext( estadoContext );
    const handleChange = ( e ) => {
        if( !e.target.files[0].type.includes('image') || !e.target.files[0].type){
            return null;
        }
        let imagen = URL.createObjectURL( e.target.files[0] );
        setImagenes( [...imagenes, imagen ] );
    }
    return (
        <div>
            {/* <label htmlFor="newImage"> + </label> */}
            <input 
                type="file"
                id="newImage"
                onChange={ handleChange }
                />
        </div>
    )
}
