
export function createCoors(x, y, radius, npoints, setCoors ) {
    let angle = (Math.PI*2) / npoints;
    let coorsTemp = [];
    for (let a = 0; a < (Math.PI*2); a += angle) {
        let sx = x + Math.cos(a) * radius;
        let sy = y + Math.sin(a) * radius;
        coorsTemp.push({sx,sy});
    }
    setCoors( coorsTemp );
}


